import React, { useState } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { Col, Row, Layout } from 'antd';
import { useQuery } from '@apollo/react-hooks';
import { Moment } from 'moment';
import { LISTING } from '../../lib/graphql/queries/Listing';
import {
  ListingBookings,
  ListingCreateBooking,
  WrappedListingCreateBookingModal as ListingCreateBookingModal,
  ListingDetails,
} from './components';
import {
  Listing as ListingData,
  ListingVariables,
} from '../../lib/graphql/queries/Listing/__generated__/Listing';
import { ErrorBanner, PageSkeleton } from '../../lib/components';
import { Viewer } from '../../lib/types';

interface MatchParams {
  id: string;
}
interface Props {
  viewer: Viewer;
}
const { Content } = Layout;
const PAGE_LIMIT = 3;

export const Listing = ({
  viewer,
  match,
}: RouteComponentProps<MatchParams> & Props) => {
  const [bookingsPage, setBookingsPage] = useState(1);
  const [checkInDate, setCheckInDate] = useState<Moment | null>(null);
  const [checkOutDate, setCheckOutDate] = useState<Moment | null>(null);
  const [modalVisible, setModalVisible] = useState(false);
  const { data, loading, error, refetch } = useQuery<
    ListingData,
    ListingVariables
  >(LISTING, {
    variables: {
      id: match.params.id,
      bookingsPage,
      limit: PAGE_LIMIT,
    },
  });

  const clearBookingData = () => {
    setModalVisible(false);
    setCheckInDate(null);
    setCheckOutDate(null);
  };
  const handleListingRefetch = async () => {
    await refetch();
  };
  if (loading) {
    return (
      <Content className='listing'>
        <PageSkeleton />
      </Content>
    );
  }
  if (error) {
    return (
      <Content className='listing'>
        <ErrorBanner description="This listing may not exist or we've encontered an error. Please try again soon!" />
        <PageSkeleton />
      </Content>
    );
  }

  const listing = data ? data.listing : null;

  const listingBookings = listing ? listing.bookings : null;

  // const listingDetailsElement = listing ? <ListingDetails listing={listing} /> : null;

  const listingDetailsElement = listing ? (
    <ListingDetails listing={listing} />
  ) : null;

  // const listingBookings = {
  //   total: 4,
  //   result: [
  //     {
  //       id: '5daa530eefc64b001767247c',
  //       tenant: {
  //         id: '117422637055829818290',
  //         name: 'User X',
  //         avatar:
  //           'https://lh3.googleusercontent.com/a-/AAuE7mBL9NpzsFA6mGSC8xIIJfeK4oTeOJpYvL-gAyaB=s100',
  //         __typename: 'User',
  //       },
  //       checkIn: '2019-10-29',
  //       checkOut: '2019-10-31',
  //       __typename: 'Booking',
  //     },
  //     {
  //       id: '5daa530eefc64b001767247d',
  //       tenant: {
  //         id: '117422637055829818290',
  //         name: 'User X',
  //         avatar:
  //           'https://lh3.googleusercontent.com/a-/AAuE7mBL9NpzsFA6mGSC8xIIJfeK4oTeOJpYvL-gAyaB=s100',
  //         __typename: 'User',
  //       },
  //       checkIn: '2019-11-01',
  //       checkOut: '2019-11-03',
  //       __typename: 'Booking',
  //     },
  //     {
  //       id: '5daa530eefc64b001767247g',
  //       tenant: {
  //         id: '117422637055829818290',
  //         name: 'User X',
  //         avatar:
  //           'https://lh3.googleusercontent.com/a-/AAuE7mBL9NpzsFA6mGSC8xIIJfeK4oTeOJpYvL-gAyaB=s100',
  //         __typename: 'User',
  //       },
  //       checkIn: '2019-11-05',
  //       checkOut: '2019-11-09',
  //       __typename: 'Booking',
  //     },
  //     {
  //       id: '5daa530eefc64b001767247f',
  //       tenant: {
  //         id: '117422637055829818290',
  //         name: 'User X',
  //         avatar:
  //           'https://lh3.googleusercontent.com/a-/AAuE7mBL9NpzsFA6mGSC8xIIJfeK4oTeOJpYvL-gAyaB=s100',
  //         __typename: 'User',
  //       },
  //       checkIn: '2019-11-10',
  //       checkOut: '2019-11-11',
  //       __typename: 'Booking',
  //     },
  //   ],
  // } as any;
  const listingBookingsElement = listingBookings ? (
    <ListingBookings
      listingBookings={listingBookings}
      bookingsPage={bookingsPage}
      limit={PAGE_LIMIT}
      setBookingsPage={setBookingsPage}
    />
  ) : null;
  const listingCreateBooking = listing ? (
    <ListingCreateBooking
      viewer={viewer}
      host={listing.host}
      bookingsIndex={listing.bookingsIndex}
      price={listing.price}
      checkInDate={checkInDate}
      checkOutDate={checkOutDate}
      setCheckInDate={setCheckInDate}
      setCheckOutDate={setCheckOutDate}
      setModalVisible={setModalVisible}
    />
  ) : null;

  const listingCreateBookingModalElement =
    listing && checkInDate && checkOutDate ? (
      <ListingCreateBookingModal
        id={listing.id}
        price={listing?.price}
        modalVisible={modalVisible}
        checkInDate={checkInDate}
        checkOutDate={checkOutDate}
        setModalVisible={setModalVisible}
        clearBookingData={clearBookingData}
        handleListingRefetch={handleListingRefetch}
      />
    ) : null;
  return (
    <Content className='listing'>
      <Row gutter={24} justify='space-between'>
        <Col xs={24} lg={14}>
          {listingDetailsElement}
          {listingBookingsElement}
        </Col>
        <Col xs={24} lg={10}>
          {listingCreateBooking}
        </Col>
      </Row>
      {listingCreateBookingModalElement}
    </Content>
  );
};
