import React, { FormEvent, useState } from 'react';
import { useMutation } from '@apollo/react-hooks';
import {
  Button,
  Form,
  Input,
  InputNumber,
  Radio,
  Layout,
  Typography,
  Upload,
} from 'antd';
import { Link, Redirect } from 'react-router-dom';
import {
  HomeOutlined,
  BankOutlined,
  LoadingOutlined,
  PlusOutlined,
} from '@ant-design/icons';
import { HOST_LISTING } from '../../lib/graphql/mutations';
import {
  HostListing as HostListingData,
  HostListingVariables,
} from '../../lib/graphql/mutations/HostListing/__generated__/HostListing';
import { Viewer } from '../../lib/types';
import { ListingType } from '../../lib/graphql/globalTypes';
import {
  iconColor,
  displayErrorMessage,
  displaySuccessNotification,
} from '../../lib/utils';
import { UploadChangeParam } from 'antd/lib/upload';

const { Content } = Layout;
const { Text, Title } = Typography;
const { Item } = Form;

interface Props {
  viewer: Viewer;
}

export const Host = ({ viewer }: Props) => {
  const [imageLoading, setImageLoading] = useState(false);
  const [imageBase64Value, setImageBase64Value] = useState<string | null>(null);

  const [hostListing, { loading, data }] = useMutation<
    HostListingData,
    HostListingVariables
  >(HOST_LISTING, {
    onCompleted: () => {
      displaySuccessNotification('Listing created successfully!');
    },
    onError: () => {
      displayErrorMessage('Unable to create listing. Please try again');
    },
  });

  const handleImageUpload = (info: UploadChangeParam) => {
    const { file } = info;

    if (file.status === 'uploading') {
      setImageLoading(true);
      return;
    }
    if (file.status === 'done' && file.originFileObj) {
      getBase64Value(file.originFileObj, (imageBase64Value) => {
        setImageBase64Value(imageBase64Value);
        setImageLoading(false);
      });
    }
  };
  const handleHostListing = (evt: FormEvent) => {
    evt.preventDefault();
  };
  const onFinish = (values: any) => {
    console.log('values', values);
    const fullAddress = `${values.address}, ${values.city}, ${values.state}, ${values.postalCode} `;
    const input = {
      ...values,
      address: fullAddress,
      image: imageBase64Value,
      price: values.price * 100,
    };

    delete input.city;
    delete input.state;
    delete input.postalCode;
    hostListing({
      variables: {
        input,
      },
    });
  };
  if (!viewer.id || !viewer.hasWallet) {
    return (
      <Content className='host-content'>
        <div className='host__form-header'>
          <Title level={4} className='host__form-title'>
            You'll have to be signed in and connected with Stripe to Host a
            listing!
          </Title>
          <Text type='secondary'>
            We only allow users who've signed into our application and have
            connected with Stripe to host new listings. You can sign in at the{' '}
            <Link to='/login'>/login</Link> page and connect with Stripe shortly
            after.
          </Text>
        </div>
      </Content>
    );
  }
  if (loading) {
    return (
      <Content className='host-content'>
        <div className='host__form-header'>
          <Title level={4} className='host__form-title'>
            Please Wait!
          </Title>
          <Text type='secondary'>We're creating your listing now.</Text>
        </div>
      </Content>
    );
  }
  if (data && data.hostListing) {
    return <Redirect to={`/listing/${data.hostListing.id}`} />;
  }
  return (
    <Content className='host-content'>
      <Form
        layout='vertical'
        onSubmitCapture={handleHostListing}
        onFinish={onFinish}
      >
        <div className='host__form-header'>
          <Title level={3} className='host__form-title'>
            Hi! Let's get started listing your place.
          </Title>
          <Text type='secondary'>
            In this form, we'll collect some basic and additional information
            about your listing.
          </Text>
        </div>
        <Item
          label='Home Type'
          name='type'
          rules={[
            {
              required: true,
              message: 'Please select a home type',
            },
          ]}
        >
          <Radio.Group>
            <Radio.Button value={ListingType.APARTMENT}>
              <BankOutlined style={{ color: iconColor, marginRight: '7px' }} />
              <span>Apartment</span>
            </Radio.Button>
            <Radio.Button value={ListingType.HOUSE}>
              <HomeOutlined style={{ color: iconColor, marginRight: '7px' }} />
              <span>House</span>
            </Radio.Button>
          </Radio.Group>
        </Item>
        <Item
          label='Max # of Guests'
          name='numOfGuests'
          rules={[
            {
              required: true,
              message: 'Please indicate max # of guests for the listing',
            },
          ]}
        >
          <InputNumber min={1} placeholder='4' />
        </Item>

        <Item
          label='Title'
          extra='Max character count of 45'
          name='title'
          rules={[
            {
              required: true,
              message: 'please enter a title for the listing',
            },
          ]}
        >
          <Input
            maxLength={45}
            placeholder='The iconic and luxurious Bel-Air mansion'
          />
        </Item>
        <Item
          label='Description of listing'
          extra='Max character count of 400'
          name='description'
          rules={[
            {
              required: true,
              message: 'please enter a description for the listing',
            },
          ]}
        >
          <Input.TextArea
            rows={3}
            maxLength={400}
            placeholder='Modern, clean, and iconic home of Fresh Prince. Situated in the heart of Bel-Air Los Angeles'
          />
        </Item>
        <Item
          label='Address'
          name='address'
          rules={[
            {
              required: true,
              message: 'please an address for the listing',
            },
          ]}
        >
          <Input placeholder='250 North Bristol Avenue' />
        </Item>
        <Item
          label='City/Town'
          name='city'
          rules={[
            {
              required: true,
              message: 'please enter a city (or region) for the listing',
            },
          ]}
        >
          <Input placeholder='Los Angeles' />
        </Item>
        <Item
          label='State/Province'
          name='state'
          rules={[
            {
              required: true,
              message: 'please enter a state (or province) for the listing',
            },
          ]}
        >
          <Input placeholder='California' />
        </Item>
        <Item
          label='Zip/Postal Code'
          name='postalCode'
          rules={[
            {
              required: true,
              message: 'please enter a postal code for the listing',
            },
          ]}
        >
          <Input placeholder='Please enter a zip code for your listing!' />
        </Item>
        <Item
          label='Image'
          extra='Images have to be under 1MB in size and of type JPG or PNG'
          name='image'
          rules={[
            {
              required: true,
              message: 'please provide an image for the listing',
            },
          ]}
        >
          <div className='host__form-image-upload'>
            <Upload
              name='image'
              listType='picture-card'
              showUploadList={false}
              action='https://www.mocky.io/v2/5cc8019d300000980a055e76'
              beforeUpload={beforeImageUpload}
              onChange={handleImageUpload}
            >
              {imageBase64Value ? (
                <img src={imageBase64Value} alt='listing' />
              ) : (
                <div>
                  {imageLoading ? <LoadingOutlined /> : <PlusOutlined />}
                  <div className='ant-upload-text'>Upload</div>
                </div>
              )}
            </Upload>
          </div>
        </Item>
        <Item
          label='Price'
          extra='All prices in $USD/day'
          name='price'
          rules={[
            {
              required: true,
              message: 'please select a price for the listing',
            },
          ]}
        >
          <InputNumber min={0} placeholder='120' />
        </Item>
        <Button type='primary' htmlType='submit'>
          Submit
        </Button>
      </Form>
    </Content>
  );
};

const beforeImageUpload = (file: File) => {
  const fileIsValidImage =
    file.type === 'image/jpeg' || file.type === 'image/png';
  const fileIsValidSize = file.size / 1024 / 1024 < 1;

  if (!fileIsValidImage) {
    displayErrorMessage("You're only able to upload valid JPG or PNG files!");
    return false;
  }
  if (!fileIsValidSize) {
    displayErrorMessage(
      "You're only able to upload image files of under 1MB in size!",
    );
    return false;
  }

  return fileIsValidImage && fileIsValidSize;
};

const getBase64Value = (
  img: File | Blob,
  callback: (imageBase64Value: string) => void,
) => {
  const reader = new FileReader();
  reader.readAsDataURL(img);
  reader.onload = () => {
    callback(reader.result as string);
  };
};
